import React, { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import BasicBtn from "../../components/UI/Button/BasicBtn";
import Card from "./../../components/UI/Card/Card";
import styles from "./login.module.css";
import { ReactComponent as Spinner } from "./../../assets/icons/Spinner.svg";
import apiUrl from "../../api";

function Login(props) {
  const history = useHistory();
  const nameInputRef = useRef();
  const emailInputRef = useRef();
  const passwordInputRef = useRef();
  const confirmInputRef = useRef();

  const [clickLogin, setClickLogin] = useState(true);
  const [clickSignup, setClickSignup] = useState(false);
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const loginRender = () => {
    setClickLogin(true);
    setClickSignup(false);
    setIsError(false);
  };

  const signupRender = () => {
    setClickLogin(false);
    setClickSignup(true);
    setIsError(false);
  };
  const submitHandler = (event) => {
    event.preventDefault();
    const enteredEmail = emailInputRef.current.value;
    const enteredPassword = passwordInputRef.current.value;
    let enteredName;
    if (enteredPassword.length < 5 || enteredPassword.length > 20) {
      setErrorMessage("Password length must be in between 5 and 20");
      setIsError(true);
      return;
    }
    if (clickSignup) {
      const enteredConfirmPassword = confirmInputRef.current.value;
      enteredName = nameInputRef.current.value;
      if (enteredPassword !== enteredConfirmPassword) {
        setErrorMessage("Password and Confirm Password are must same");
        passwordInputRef.current.value = "";
        confirmInputRef.current.value = "";
        setIsError(true);
        return;
      }
    }
    setIsLoading(true);
    clickLogin
      ? fetch(`${apiUrl}/accounts/login/`, {
          method: "POST",
          body: JSON.stringify({
            email: enteredEmail,
            password: enteredPassword,
          }),
          headers: {
            "Content-Type": "application/json",
          },
        })
          .then((res) => {
            setIsLoading(false);
            if (!res.ok) {
              setIsError(true);
              setErrorMessage("Something went wrong");
            }

            return res.json();
          })
          .then((data) => {
            if (data === "User not found") {
              setIsError(true);
              setErrorMessage("Email and Password is incorrect");
              return;
            }
            document.cookie = `tokens=${data.tokens}; expires=Sun, 1 Jan 2023 00:00:00 UTC; path=/`;
            console.log(data);
          })
          .catch((err) => {
            setErrorMessage(err.toString());
            console.log(err);
          })
      : fetch(`${apiUrl}/accounts/users/`, {
          method: "POST",
          body: JSON.stringify({
            email: enteredEmail,
            first_name: enteredName,
            password: enteredPassword,
          }),
          headers: {
            "Content-Type": "application/json",
          },
        })
          .then((res) => {
            setIsLoading(false);
            if (!res.ok) {
              setIsError(true);
              setErrorMessage("Something went wrong");
              return;
            }
            return res.json();
          })
          .then((data) => {
            if (!data.tokens) {
              return;
            }
            document.cookie =
              "tokens=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/";
            document.cookie = `tokens=${data.tokens.access}; expires=Sun, 1 Jan 2023 00:00:00 UTC; path=/`;
            console.log(data);
          })
          .catch((err) => console.log(err));

    emailInputRef.current.value = "";
    passwordInputRef.current.value = "";
    if (clickSignup) {
      confirmInputRef.current.value = "";
      nameInputRef.current.value = "";
    }
  };
  function clearError() {
    setIsError(false);
  }
  return (
    <Card>
      <div className={styles.container}>
        <div className={styles.heading}>
          <h1
            className={`${styles.heading__login} ${
              clickLogin && styles.heading__active
            }`}
            onClick={loginRender}
          >
            Login
          </h1>
          <h1
            className={`${styles.heading__signup} ${
              clickSignup && styles.heading__active
            }`}
            onClick={signupRender}
          >
            Signup
          </h1>
        </div>
        <form className={styles.form} onSubmit={submitHandler}>
          {clickSignup && (
            <div className={styles.form__control}>
              <input
                type="text"
                id="name"
                placeholder="Full Name"
                required
                ref={nameInputRef}
                className={styles.input}
                onClick={clearError}
              />
              <label htmlFor="name" className={styles.input__label}>
                Full Name
              </label>
            </div>
          )}
          <div className={styles.form__control}>
            <input
              type="email"
              id="email"
              placeholder="Email"
              required
              ref={emailInputRef}
              className={styles.input}
              onClick={clearError}
            />{" "}
            <label htmlFor="email" className={styles.input__label}>
              Email
            </label>
          </div>
          <div className={styles.form__control}>
            <input
              type="password"
              id="password"
              required
              placeholder="Password"
              ref={passwordInputRef}
              className={styles.input}
              onClick={clearError}
            />
            <label htmlFor="password" className={styles.input__label}>
              Password
            </label>
          </div>
          {clickSignup && (
            <div className={styles.form__control}>
              <input
                type="password"
                id="passwordConfirm"
                required
                placeholder="Confirm Password"
                ref={confirmInputRef}
                className={styles.input}
                onClick={clearError}
              />
              <label htmlFor="passwordConfirm" className={styles.input__label}>
                Confirm Password
              </label>
            </div>
          )}
          {isError && (
            <div className={styles.form__control}>
              <p className={styles.errorMessage}>{errorMessage}</p>
              {console.log(errorMessage)}
            </div>
          )}
          {!isLoading ? (
            <BasicBtn
              btnName={clickLogin ? "Login" : "Signup"}
              className={styles.login__btn}
            />
          ) : (
            <Spinner />
          )}
        </form>
      </div>
    </Card>
  );
}

export default Login;
