import React,{Fragment, useState, useEffect} from "react";
import HeaderBanner from "../../components/Header/Banner/HeaderBanner";
import CategoryContainer from "../../components/UI/Container/CategoryContainer/CategoryContainer";
import Security from "../../components/Layout/Security/Security";
import ProductWidget from "../../components/Layout/Widgets/ProductWidget";
import AllProducts from "./Sections/AllProduct";
import styles from "./Homepage.module.css";
import apiUrl from "../../api";

//data
import TrendingData from "./../../Data/TrendingProductsData";
import CateDataHome from "../../Data/CateDataHome";
import WheyProtienData from "../../Data/WheyProtienData";

function Homepage() {

  return (
    <Fragment>
      <HeaderBanner />
      {/* <section className={styles.section}>
          <ProductWidget heading="Trending Now" productData={TrendingData} />
        </section> */}
      <AllProducts
        heading="Trending Now"
        url={`${apiUrl}/shop/products/?limit=4`}
      ></AllProducts>
      <section className={styles.section}>
        <CategoryContainer
          title="Hi, What are you looking for?"
          items={CateDataHome}
        ></CategoryContainer>
      </section>
      {/* <section className={styles.section}>
          <ProductWidget
            heading="Trending In Whey Protien"
            productData={WheyProtienData}
          />
        </section> */}
      <AllProducts
        heading="Trending In Whey Protien"
        url={`${apiUrl}/shop/products/?limit=4`}
      ></AllProducts>
      {/* <section className={styles.section}>
          <ProductWidget
            heading="All Products"
            productData={WheyProtienData}
          />
        </section> */}
      <AllProducts
        heading="All Products"
        url={`${apiUrl}/shop/products/?limit=4`}
      ></AllProducts>
      <section className={styles.section}>
        <Security />
      </section>
    </Fragment>
  );
}

export default Homepage;
