import { useState } from "react";
import { Link } from "react-router-dom";
import styles from "./DropdownModel.module.css";
import ShopBy from "./DifferentModel/ShopBy";
import Brands from "./DifferentModel/Brands";
import CategoryData from "../../../../Data/CategoryData";

function DropdownModel(props) {
  // console.log(props.categoryData);
  // console.log(props.number);
  if (!props.categoryData) {
    props.categoryData = CategoryData;
  }
  console.log("dropdown props ", props);
  return (
    <div className={styles.container} onMouseLeave={props.onMouseLeave}>
      <div className={styles.details}>
        {/* {props.number === 1 ? (
          <ShopBy categoryData={props.categoryData} onClick={props.onClick}></ShopBy>
        ) : (
          <Brands categoryData={props.categoryData}></Brands>
        )} */}
        {
          {
            1: (
              <ShopBy
                categoryData={props.categoryData}
                onClick={props.onClick}
              ></ShopBy>
            ),
            // 1:<Brands categoryData={props.categoryData}></Brands>,
            2: <Brands categoryData={props.categoryData}></Brands>,
          }[props.number]
        }
      </div>
    </div>
  );
}

export default DropdownModel;
