import React, { useState, useEffect } from "react";
import styles from "./DropDown.module.css";
import { ReactComponent as Down } from "./../../../../assets/icons/down-outline.svg";
import DropdownModel from "../DropdownModel/DropdownModel";
import ThreeLine from "../../../UI/ThreeLine/ThreeLine";
import apiUrl from "../../../../api";

const DropDown = (props) => {
  const [fixNav, setFixNav] = useState(false);
  const [open, setOpen] = useState(false);
  const [number, setNumber] = useState(1);
  const [categoryData, setCategoryData] = useState();
  useEffect(() => {
    async function fetchData() {
      try {
        const response = await fetch(`${apiUrl}/shop/all_categories`);
        const data = await response.json();
        // console.log("fetching data");
        setCategoryData(data);
      } catch (err) {
        console.log(err);
      }
    }
    fetchData();
  }, []);
  function dropDownHandler(e) {
    setOpen(true);
    setNumber(+e.target.dataset.number);
  }

  function dropDownClose() {
    setOpen(false);
  }

  function fixNavbar() {
    if (window.scrollY >= 80) {
      setFixNav(true);
    } else {
      setFixNav(false);
    }
  }
  window.addEventListener("scroll", fixNavbar);
  return (
    <div
      className={`${styles.nav__center} ${
        fixNav ? styles.fixnav : styles.none
      }`}
    >
      <nav className={styles.nav}>
        <div
          className={`${styles.shopby} ${
            open && number === 1 ? styles.nav__active : ""
          }`}
          onMouseEnter={categoryData && dropDownHandler}
          data-number="1"
        >
          <ThreeLine />
          Shop By
        </div>
        <div
          className={`${styles.nav__item} ${
            open && number === 2 ? styles.nav__active : ""
          }`}
          onMouseEnter={categoryData && dropDownHandler}
          data-number="2"
        >
          Brands
          <div className={styles.nav__down}>
            <Down className={styles["nav__down--icon"]}></Down>
          </div>
        </div>
        <div
          className={`${styles.nav__item} ${
            open && number === 3 ? styles.nav__active : ""
          }`}
          onMouseEnter={categoryData && dropDownHandler}
          data-number="3"
        >
          Protein
          <div className={styles.nav__down}>
            <Down className={styles["nav__down--icon"]}></Down>
          </div>
        </div>
        <div
          className={`${styles.nav__item} ${
            open && number === 4 ? styles.nav__active : ""
          }`}
          onMouseEnter={categoryData && dropDownHandler}
          data-number="4"
        >
          GAT
          <div className={styles.nav__down}>
            <Down className={styles["nav__down--icon"]}></Down>
          </div>
        </div>
        <div
          className={`${styles.nav__item} ${
            open && number === 5 ? styles.nav__active : ""
          }`}
          onMouseEnter={categoryData && dropDownHandler}
          data-number="5"
        >
          Celutus
          <div className={styles.nav__down}>
            <Down className={styles["nav__down--icon"]}></Down>
          </div>
        </div>
        {categoryData ? (
          open ? (
            <DropdownModel
              onMouseLeave={dropDownClose}
              categoryData={categoryData}
              number={number}
              onClick={dropDownClose}
            />
          ) : (
            ""
          )
        ) : (
          "Loading..."
        )}
      </nav>
    </div>
  );
};

export default DropDown;
