import React, { useState, useEffect, useRef } from "react";
import styles from "./HeaderBanner.module.css";
import Slides from "../../UI/Slides/Slides";
import apiUrl from "../../../api";

function HeaderBanner(props) {
  const initalState = 0;
  const [showImage, setShowImage] = useState(initalState);

  //for automatically render next image
  const counterRef = useRef(initalState);

  const [bannersData, setBannersData] = useState([]);
  const [mainBanners, setMainBanners] = useState([]);
  const [smallBanners, setSmallBanners] = useState([]);
  const [longBanners, setLongBanners] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  function fetchData() {
    fetch(`${apiUrl}/shop/banners/`)
      .then((res) => res.json())
      .then((data) => {
        setBannersData(data);
        console.log(bannersData);
        let mb = [];
        let sb = [];
        let lb = [];
        data.map((banner) => {
          if (banner.banner_type === "main") {
            mb.push(banner);
          }
          if (banner.banner_type === "small") {
            sb.push(banner);
          }
          if (banner.banner_type === "long") {
            lb.push(banner);
          }
        });
        setMainBanners(mb);
        setSmallBanners(sb);
        setLongBanners(lb);
        // console.log(mb, sb, lb);
        setIsLoading(false);
      })
      .catch((err) => console.log(err));
  }
  useEffect(() => {
    fetchData();
  }, []);

  useEffect(() => {
    counterRef.current = showImage;
    if (counterRef.current === mainBanners.length - 1) {
      counterRef.current = 0;
    } else {
      counterRef.current = counterRef.current + 1;
    }
  });

  useEffect(() => {
    setInterval(() => {
      setShowImage(counterRef.current);
    }, 5000);
  }, []);

  // for prev slide image
  function prev() {
    showImage === 0
      ? setShowImage(mainBanners.length - 1)
      : setShowImage(showImage - 1);
  }
  // for next slide image
  function next() {
    showImage === mainBanners.length - 1
      ? setShowImage(0)
      : setShowImage(showImage + 1);
  }
  //for dot select to show image
  function selectImageShow(index) {
    setShowImage(index);
  }

  return (
    <div className={styles.banner__center}>
      {!isLoading && (
        <div className={styles.header__banners}>
          <Slides
            images={mainBanners}
            currentImage={showImage}
            prevClick={prev}
            nextClick={next}
            imageRender={(index) => selectImageShow(index)}
          >
            <div className={styles.banner__left}>
              {mainBanners && mainBanners.map((obj, index) => (
                <img
                  key={index}
                  src={obj.image}
                  alt="banner"
                  className={`${styles.banner__img} ${styles.banner__hide} ${
                    showImage === index
                      ? styles.banner__unhide
                      : styles.banner__hide
                  }`}
                />
              ))}
            </div>
          </Slides>
          {smallBanners && (
            <div className={styles.banner__right}>
              <div className={styles["banner__right--1"]}>
                <img
                  src={smallBanners[0].image}
                  alt="banner"
                  className={styles.banner__img}
                />
              </div>
              <div className={styles["banner__right--2"]}>
                <img
                  src={smallBanners[1].image}
                  alt="banner"
                  className={styles.banner__img}
                />
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default HeaderBanner;
