import { useState } from "react";
import styles from "./ProductImgContainer.module.css";

import Arrow from "../../Arrow/Arrow";

function ProductImgContainer(props) {
  const [featureImage, setFeatureImage] = useState(props.url);
  const featureImageHandler = (e)=>{
    setFeatureImage(e.target.src);

  }
  console.log(props)
  return (
    <div className={styles.container}>
      <div className={styles["top-slider"]}>
        <div className={styles.imageContainer}>
          <img
            src={featureImage}
            alt={props.alt}
            className={styles["top-img"]}
          />
        </div>
      </div>
      <div className={styles["bottom-slider__container"]}>
        {/* <Arrow> */}
        <div className={styles["bottom-slider"]}>
          {props.images && props.images.map((img, idx) => (
            <div
              className={styles["bottom-img__container"]}
              key={idx}
              onClick={featureImageHandler}
            >
              <img
                src={img.image}
                alt={img.alt}
                className={styles["bottom-img"]}
                key={idx}
              />
            </div>
          ))}
        </div>
        {/* </Arrow> */}
      </div>
    </div>
  );
}

export default ProductImgContainer;