import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import AllProductWidget from "../../components/Layout/Widgets/AllProductWidget";
import styles from "./AllProductsPage.module.css";
import { ReactComponent as Spinner } from "./../../assets/icons/Spinner.svg";
import apiUrl from "../../api";

function AllProductsPage(props) {
  const [allProducts, setAllProducts] = useState();
  const [loading, setLoading] = useState(true);
  const { slug } = useParams();
  const fetchData = async () => {
    try {
      const response = await fetch(
        `${
          props.product === "allProducts"
            ? `${apiUrl}/shop/products?limit=100`
            : `${apiUrl}/shop/products/${slug}`
        }`
      );
      const data = await response.json();
      console.log(data);
      if (data.results) {
        setAllProducts(data.results);
      } else {
        setAllProducts(data);
      }

      setLoading(false);
    } catch (err) {
      console.log(
        "something went wrong during fetching all products data",
        err
      );
    }
  };
  useEffect(() => {
    fetchData();
  }, [slug]);

  return (
    <section className={styles.section}>
      <div className={styles.heading__container}>
        <h2 className={styles.heading}>All Products {slug && 'of'} {slug}</h2>
      </div>
      {loading ? <Spinner /> : <AllProductWidget productData={allProducts} />}
      {console.log(allProducts)}
    </section>
  );
}

export default AllProductsPage;
