// import { ReactComponent as ArrowBackward } from "./../../../assets/icons/arrow-back-circle-outline.svg";
import { ReactComponent as ArrowBackward } from "./../../../assets/icons/chevron-back-outline.svg";
// import { ReactComponent as ArrowForward } from "./../../../assets/icons/arrow-forward-circle-outline.svg";
import { ReactComponent as ArrowForward } from "./../../../assets/icons/chevron-forward-outline.svg";
import styles from "./Slides.module.css";

function Slides(props) {
  const currentImage = props.currentImage;
    const dotSeletct =(index)=>{
        props.imageRender(index)
    }
  return (
    <div className={styles.slides__container}>
      <ArrowBackward
        className={`${styles.arrow__icon} ${styles["arrow__icon-left"]}`}
        onClick={props.prevClick}
      />
      <ArrowForward
        className={`${styles.arrow__icon} ${styles["arrow__icon-right"]}`}
        onClick={props.nextClick}
      />
      <div className={styles.dots}>
        {props.images && props.images.map((dot, index) => (
          <div
            key={index}
            onClick={() => dotSeletct(index)}
            className={`${styles.dot} ${
              currentImage === index ? styles.dot__seletct : styles.dot
            }`}
          ></div>
        ))}
      </div>
      {props.children}
    </div>
  );
}

export default Slides;
