import { Link } from "react-router-dom";
import styles from "./../DropdownModel.module.css";
import brandClasses from "./Brands.module.css";

function Brands(props) {
  return (
    <div className={brandClasses.container}>
      <div className={styles.details__list}>
        {props.categoryData &&
          props.categoryData.map((data, index) => {
            return (
              <ul key={index} className={styles.list}>
                <li className={`${styles.list__category} ${styles.list__item}`}>
                  <Link to={`/products/${data.slug}`}>
                    <span>{data.name}</span>
                  </Link>
                  <ul className={`${styles.list} ${styles.list__brand}`}>
                    {data.brands &&
                      data.brands.map((brand, idx) => (
                        <li key={idx} className={styles.list__item}>
                          <Link to={`/products/${brand.slug}`}>
                            {brand.name}
                          </Link>
                        </li>
                      ))}
                  </ul>
                </li>
              </ul>
            );
          })}
      </div>
    </div>
  );
}

export default Brands;
