import ReactDOM from "react-dom";
import { Route, Switch, useLocation, useRouteMatch } from "react-router-dom";
import Nav from "./components/Header/Nav/Nav";
import PageWrapper from "./components/UI/Wrapper/PageWrapper";
import Footer from "./components/Layout/Footer";
import styles from "./App.module.css";
import Homepage from "./Pages/Homepage/Homepage";
import SingleProduct from "./Pages/Product/SingleProduct";
import Policies from "./Pages/Policies/Policies";
import Login from "./Pages/Auth/Login";
import CartPage from "./Pages/CartPage/CartPage";
import AllProductsPage from "./Pages/Product/AllProductsPage";
import ScrollToTop from "./components/UI/ScrollToTop/ScrollToTop";
import MobileNav from "./components/Header/Nav/MainNav/MobileNav";
import CategoryContainer from "./components/UI/Container/CategoryContainer/CategoryContainer";
import CategoryData from "./Data/CategoryData";
import AllProducts from "./Pages/Homepage/Sections/AllProduct";

function App() {
  const location = useLocation();
  const match = useRouteMatch();
  console.log(location);
  console.log('match data ',match);
  console.log(location.phatname === "product/:slug");
  return (
    <PageWrapper>
      <Nav />
      <main className={styles.main__content}>
        <ScrollToTop />
        <Switch>
          <Route path="/product/:slug" exact>
            <SingleProduct />
            
          </Route>
          <Route path="/products" exact>
            <AllProductsPage product="allProducts" />
          </Route>
          <Route path="/products/:slug" exact>
            <AllProductsPage product="productBy" />
          </Route>
          <Route path="/login" exact>
            <Login />
          </Route>
          <Route path="/policies/" exact>
            <Policies></Policies>
          </Route>
          <Route path="/policies/:policy" exact>
            <Policies></Policies>
          </Route>
          <Route path="/checkout/cart" exact>
            <CartPage></CartPage>
          </Route>
          <Route path="/category">
            <CategoryContainer
              title="Hi, What are you looking for?"
              items={CategoryData}
            ></CategoryContainer>
          </Route>
          <Route path="/" exact>
            <Homepage></Homepage>
          </Route>
        </Switch>
      </main>
      <div className={styles.footer__content}>
        <Footer></Footer>
      </div>
      {location.phatname === "product/:slug"}
      {ReactDOM.createPortal(
        <MobileNav></MobileNav>,
        document.getElementById("mobile-nav-root")
      )}
    </PageWrapper>
  );
}

export default App;