import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import Card from "./../UI/Card/Card";
import Wishlist from "../UI/Wishlist/Wishlist";
import AddCartBtn from "../UI/Button/AddCartBtn";
import VegNonveg from "../UI/VegNonveg/VegNonveg";
import BasicBtn from "../UI/Button/BasicBtn";
import apiUrl from "../../api";

import styles from "./ProductItem.module.css";
function ProductItem(props) {
  const [isSending, setIsSending] = useState(false);
  const history = useHistory();
  const addCartHandler = () => {
    setIsSending(true);
    function getCookie(name) {
      const value = `; ${document.cookie}`;
      const parts = value.split(`; ${name}=`);
      if (parts.length === 2) return parts.pop().split(";").shift();
    }

    fetch(`${apiUrl}/cart/cart/`, {
      method: "POST",
      // mode: "cors",
      // credentials: "include",
      headers: {
        "Content-Type": "application/json",

        // Authorization: `Bearer ${getCookie("tokens")}`,
        // Cookie: "sessionid=xetdp6vgo3q5ck5jax1rp6i2emvxut70",
      },
      body: JSON.stringify({
        quantity: +1,
        override: false,
        product_slug: props.slug,
        variant_slug: props.variantSlug,
        flavour_slug: props.flavorSlug,
        weight_slug: props.weightSlug,
      }),
    })
      .then((res) => {
        console.log(res);
        return res.json();
      })
      .then((data) => {
        console.log("cart data for posting", data);
        // setIsSending(false);
        // history.push("/checkout/cart");
      })
      .catch((err) => {
        setIsSending(false);
        console.log(err);
      });
    // if (!isSending) {
    //   // history.push("/checkout/cart");
    // }
  };

  return (
    <Card>
      <div className={styles.card}>
        <div className={styles.card_topicon}>
          <div className={styles.vegnonveg__conatainer}>
            <VegNonveg type={props.type} />
          </div>
          <div className={styles.wishlist}>
            <Wishlist></Wishlist>
          </div>
        </div>
        <div className={styles.card__image}>
          <Link to={`/product/${props.slug}`}>
            <img
              src={props.src}
              alt={props.alt}
              className={styles["card__image-item"]}
            />
          </Link>
        </div>
        <div className={styles.offer}>{props.off}% Off</div>

        <Link to={`/product/${props.slug}`}>
          <div className={styles.card__heading}>{props.name}</div>
        </Link>
        <div className={styles.card__price}>
          <span className={styles.price__current}>&#8377; {props.price}</span>{" "}
          <span className={styles.price__old}>&#8377; {props.oldPrice}</span>
        </div>
        {isSending ? (
          <Link to="/checkout/cart" style={{ display: "contents" }}>
            <BasicBtn btnName="View Cart" className="viewCart"/>
          </Link>
        ) : (
          <AddCartBtn onClick={addCartHandler} />
        )}
      </div>
    </Card>
  );
}

export default ProductItem;
