import { useState, useEffect } from "react";
import { useParams, Link, useHistory, useLocation } from "react-router-dom";
import ProductImgContainer from "../../components/UI/Container/ProductImgContainer/ProductImgContainer";
import AddCartBtn from "../../components/UI/Button/AddCartBtn";
import Card from "../../components/UI/Card/Card";
import styles from "./SingleProduct.module.css";
import ProductImgData from "../../Data/ProductImgData";
import singleData from "./../../Data/SingleProductData";
import { ReactComponent as Spinner } from "./../../assets/icons/Spinner.svg";
import apiUrl from "../../api";

function SingleProduct() {
  let { slug } = useParams();
  let history = useHistory();
  const location = useLocation();
  console.log("location data .. ", location);
  let productSlug = slug;
  const [loading, setLoading] = useState(true);
  const [isSending, setIsSending] = useState(false);
  const [item, setItem] = useState();
  const [selectedWeight, setSelectedWeight] = useState(0);
  const [selectedWeightName, setSelectedWeightName] = useState("");
  const [selectedWeightSlug, setSelectedWeightSlug] = useState("");
  const [selectedFlavourName, setSelectedFlavourName] = useState(
    item ? item.flavours[0].flavour : ""
  );
  const [selectedFlavor, setSelectedFlavor] = useState(0);
  const [selectedFlavorSlug, setSelectedFlavorSlug] = useState("");
  const [cartCount, setCartCount] = useState(1);
  const [selectedVariantSlug, setSelectedVariantSlug] = useState("");

  const increaseCount = () => {
    setCartCount(cartCount + 1);
  };
  const decreaseCount = () => {
    if (cartCount > 1) {
      setCartCount(cartCount - 1);
    }
  };

  const setingInitialState = (data) => {
    selectedVariantSlug === "" &&
      setSelectedVariantSlug(data.product_variants[0].slug);
    selectedWeightSlug === "" &&
      setSelectedWeightSlug(data.product_variants[0].weight.slug);
    selectedFlavorSlug === "" &&
      setSelectedFlavorSlug(data.product_variants[0].flavour[0].slug);
  };

  const addCartHandler = () => {
    setIsSending(true);

    function getCookie(name) {
      const value = `; ${document.cookie}`;
      const parts = value.split(`; ${name}=`);
      if (parts.length === 2) return parts.pop().split(";").shift();
    }
    console.log(getCookie("tokens"));
    console.log(
      cartCount,
      productSlug,
      selectedVariantSlug,
      selectedFlavorSlug,
      selectedWeightSlug
    );
    fetch(`${apiUrl}/cart/cart/`, {
      method: "POST",
      // mode:"cors",
      headers: {
        "Content-Type": "application/json",
        // Authorization: `Bearer ${getCookie("tokens")}`,
        // Cookie: "sessionid=xetdp6vgo3q5ck5jax1rp6i2emvxut70",
      },
      body: JSON.stringify({
        quantity: +cartCount,
        override: false,
        product_slug: productSlug,
        variant_slug: selectedVariantSlug,
        flavour_slug: selectedFlavorSlug,
        weight_slug: selectedWeightSlug,
      }),
    })
      .then((res) => {
        setIsSending(false);
        console.log(res);
        return res.json();
      })
      .then((data) => console.log("cart data for posting", data))
      .catch((err) => console.log(err));
    if (!isSending) {
      // history.push("/checkout/cart");
    }
  };

  const weightSlectionHandler = (e) => {
    setSelectedWeight(+e.target.dataset.id);
    setSelectedWeightName(e.target.dataset.weightname);
    setSelectedWeightSlug(e.target.dataset.weightslug);
    setSelectedVariantSlug(e.target.dataset.variantslug);
    setSelectedFlavor(0);
  };

  const flavourSelectionHandler = (e) => {
    setSelectedFlavor(+e.target.dataset.id);
    setSelectedFlavourName(e.target.dataset.flavourname);
    setSelectedFlavorSlug(e.target.dataset.flavourslug);
    console.log(
      selectedWeightName,
      selectedFlavourName,
      "names",
      e.target.dataset
    );
  };

  const fetchData = async () => {
    const response = await fetch(`${apiUrl}/shop/product/${slug}`);
    const data = await response.json();
    setingInitialState(data);
    setItem({
      data: data,
      productName: data.name,
      price:
        data.product_variants[selectedWeight].flavour[selectedFlavor]
          .selling_price,
      oldPrice:
        data.product_variants[selectedWeight].flavour[selectedFlavor].mrp_price,
      brand: data.brand.name,
      off: data.product_variants[selectedWeight].flavour[selectedFlavor]
        .discount,
      weights: data.product_variants,
      flavours: data.product_variants[selectedWeight].flavour,
      offerList: singleData.offerList,
      benefits: singleData.benefits,
      productDetails: singleData.productDetails,
      featureImage: data.product_variants[selectedWeight].feature_image,
      productVariants: data.product_variants,
    });
    setLoading(false);
  };
  useEffect(() => {
    fetchData();
  }, [selectedWeight, selectedFlavor]);

  return (
    <section className="section">
      {loading ? (
        <div>
          <Spinner></Spinner>
        </div>
      ) : (
        <div className={styles.container}>
          <div className={styles.breadcum}>
            <Link to="/"> Home</Link> / <Link to="/products"> Products</Link> /
            <Link to={`/products/${item.data.category.slug}`}>
              {" "}
              {item.data.category.name}
            </Link>{" "}
            /<Link to={`/products/${item.data.brand.slug}`}> {item.brand}</Link>
          </div>
          {console.log(item.weights[selectedWeight].weight.weight)}
          <div className={styles.product__summary}>
            <div className={styles["product__summary-left"]}>
              <div className={styles.product__imgContainer}>
                <ProductImgContainer
                  url={item.featureImage ? item.featureImage : ""}
                  alt={item.name ? item.name : ""}
                  images={
                    item.productVariants &&
                    item.productVariants[selectedWeight].images
                  }
                ></ProductImgContainer>
              </div>
              <div className={styles.product__benefits}>
                <div>Product Benefits</div>
                <div className={styles["product__benefits-text"]}>
                  {item &&
                    item.benefits &&
                    item.benefits.text &&
                    item.benefits.text.map((text, index) => {
                      return (
                        <div>
                          <span className={styles.benefits__icon}></span>
                          <li className={styles.benefits__text}>{text}</li>
                        </div>
                      );
                    })}
                </div>
                <div className={styles["product__details-highlights"]}>
                  {/* {item.highlights.map((highlight, index) => {
                  return (
                    <span className={styles.highlight__box}>
                      <span className={styles.highlight__quantity}>
                        {highlight.quantity}
                      </span>
                      <span className={styles.highlight__description}>
                        {highlight.description}
                      </span>
                    </span>
                  );  
                })} */}
                </div>
              </div>
            </div>
            <div className={styles.product__content}>
              <h2 className={styles.product__heading}>
                {item.productName}{" "}
                {selectedWeightName === ""
                  ? `, ${item.weights[0].weight.weight} ,`
                  : `, ${selectedWeightName} ,`}{" "}
                {selectedFlavourName === ""
                  ? item.flavours[0].flavour
                  : selectedFlavourName}
              </h2>
              <div>
                By{" "}
                <Link to={`/products/${item.data.brand.slug}`}>
                  <span className={styles.product__brand}>{item.brand}</span>
                </Link>
              </div>
              <div className={styles.product__off}>{item.off}% OFF</div>
              <div className={styles.product__prices}>
                <span className={styles["product__current-price"]}>
                  &#8377; {item.price}
                </span>
                <span className={styles["product__old-price"]}>
                  &#8377; {item.oldPrice}
                </span>
              </div>

              <div
                className={`${styles["weight-heading"]} ${styles["options-heading"]}`}
              >
                Weight
              </div>
              <div
                className={`${styles["weight-container"]} ${styles["options-container"]}`}
              >
                {item &&
                  item.weights &&
                  item.weights.map((variant, index) => {
                    return (
                      <span
                        className={`${styles["options-span"]} ${
                          selectedWeight === index
                            ? styles["options__selected"]
                            : ""
                        }`}
                        key={index}
                        data-id={index}
                        data-weightname={variant.weight.weight}
                        data-weightslug={variant.weight.slug}
                        data-variantslug={variant.slug}
                        onClick={weightSlectionHandler}
                      >
                        {variant.weight.weight}
                      </span>
                    );
                  })}
              </div>
              <div
                className={`${styles["flavour-text"]} ${styles["options-heading"]}`}
              >
                Select Flavour From ({item.flavours.length} Options)
              </div>
              <div
                className={`${styles["flavour-container"]} ${styles["options-container"]}`}
              >
                {item &&
                  item.flavours &&
                  item.flavours.map((flavour, index) => {
                    return (
                      <span
                        className={`${styles["options-span"]} ${
                          selectedFlavor === index
                            ? styles["options__selected"]
                            : ""
                        }`}
                        key={index}
                        data-id={index}
                        data-flavourname={flavour.flavour}
                        data-flavourslug={flavour.slug}
                        onClick={flavourSelectionHandler}
                      >
                        {flavour.flavour}
                      </span>
                    );
                  })}
              </div>
              <div className={styles.stickyCart}>
                {isSending ? (
                  <Spinner></Spinner>
                ) : (
                  <div className={styles["product__add-cart"]}>
                    <div className={styles["product__quantity-control"]}>
                      <span
                        className={styles["decrease"]}
                        onClick={decreaseCount}
                      >
                        <span className={styles["decrease__button"]}></span>
                      </span>
                      <span className={styles["quantity-number"]}>
                        {cartCount}
                      </span>
                      <span className={styles.increase} onClick={increaseCount}>
                        <span className={styles["increase__button"]}></span>
                      </span>
                    </div>

                    <AddCartBtn
                      singlePage={styles.cartBtn}
                      cartIcon={styles.cartIcon}
                      onClick={addCartHandler}
                    />
                  </div>
                )}
              </div>
              <div
                className={`${styles["offer-available"]} ${styles["options-heading"]}`}
              >
                Offer Available
              </div>
              <div className={styles["offer-list"]}>
                {item &&
                  item.offerList &&
                  item.offerList.map((offer, index) => {
                    return (
                      <div key={index}>
                        {" "}
                        <span
                          className={styles.list__icon}
                          key={index}
                        ></span>{" "}
                        {offer}
                      </div>
                    );
                  })}
              </div>
            </div>
          </div>
          <div className={styles.product__details}>
            <h3 className={styles["product__details-heading"]}>
              Product Details
            </h3>
            <div className={styles["product__details-list"]}>
              {item &&
                item.productDetails &&
                item.productDetails.map((list, index) => {
                  return (
                    <div key={index}>
                      {" "}
                      <span
                        className={styles.list__icon}
                        key={index}
                      ></span>{" "}
                      {list}
                    </div>
                  );
                })}
            </div>
          </div>
          <div className={styles.product__info}>
            <h3 className={styles["product__small-heading"]}>Product Info</h3>
          </div>
          <div className={styles.product__faq}>Product FAQ</div>
          <div className={styles.product__similar}>Similar Products</div>
        </div>
      )}
    </section>
  );
}

export default SingleProduct;
