import { Link } from "react-router-dom";
import { ReactComponent as Right } from "./../../../../../assets/icons/down-outline.svg";
import styles from "./ShopBy.module.css";
import classes from "./../DropdownModel.module.css";
import { useState } from "react";
import CategoryData from "../../../../../Data/CategoryData";

function ShopBy(props) {
  if (!props.categoryData) {
    props.categoryData = CategoryData;
  }
  console.log("shopby in props .. ", props);
  const [currentHover, setCurrentHover] = useState(0);
  // const currentHover = 0;
  const renderRight = (e) => {
    setCurrentHover(
      isNaN(e.target.dataset.current) ? +1 : +e.target.dataset.current
    );
    console.log(e.target);
  };
  return (
    <div>
      {props.categoryData !== undefined && (
        <div className={styles.container}>
          <div className={styles.left}>
            {props.categoryData &&
              props.categoryData.length > 0 &&
              props.categoryData.map((data, index) => {
                return (
                  <ul key={index} className={classes.list}>
                    <li
                      className={`${classes.list__category} ${styles.list__item}`}
                      onClick={props.onClick}
                    >
                      <Link
                        to={`/products/${data.slug}`}
                        className={`${styles.category__name} ${
                          index === currentHover ? styles.active : ""
                        }`}
                        data-current={index}
                        onMouseEnter={renderRight}
                      >
                        {console.log(
                          index,
                          "index and currentHover ",
                          currentHover
                        )}
                        <span>{data.name}</span>
                        <Right className={styles.icon}></Right>
                      </Link>
                    </li>
                  </ul>
                );
              })}
          </div>
          <div className={styles.right}>
            <div className={classes.details__list}>
              {props.categoryData &&
              props.categoryData[currentHover].sub_categories &&
              props.categoryData[currentHover].sub_categories.length > 0
                ? props.categoryData[currentHover].sub_categories.map(
                    (sub, idx2) => {
                      return (
                        <ul className={classes.list} key={idx2 + 4}>
                          <Link to={`/products/${sub.slug}`} key={idx2}>
                            <li
                              className={`${classes.list__category} ${classes.list__item}`}
                              key={idx2}
                              onClick={props.onClick}
                            >
                              {sub.name}
                            </li>
                          </Link>
                          <ul
                            className={`${classes.list} ${classes.list__brand}`}
                            key={idx2 + 8}
                          >
                            {sub.sub_sub_categories.length > 0 &&
                              sub.sub_sub_categories.map((subSub, idx3) => {
                                return (
                                  <Link
                                    to={`/products/${subSub.slug}`}
                                    key={idx3}
                                  >
                                    <li
                                      key={idx3}
                                      className={classes.list__item}
                                      onClick={props.onClick}
                                    >
                                      {subSub.name && subSub.name}
                                    </li>
                                  </Link>
                                );
                              })}
                          </ul>
                        </ul>
                      );
                    }
                  )
                : ""}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default ShopBy;
