import Container from "../../UI/Container/Container";
import ProductItem from "../../Items/ProductItem";
import Arrow from "../../UI/Arrow/Arrow";
import styles from "./ProductWidget.module.css";

const ProductWidget = (props) => {
    console.log(props.productData);
  return (
    <Arrow>
      <Container heading={props.heading} linkTo={props.linkTo}>
        <div className={styles.items}>
          {props.productData && props.productData.map((data, index) => (
            <ProductItem
              name={
                data.name.trim().length < 47
                  ? data.name.trim()
                  : `${data.name.trim().substring(0, 46)}...`
              }
              src={data.product_variants[0].feature_image}
              alt={data.name}
              price={data.product_variants[0].flavour[0].selling_price}
              oldPrice={data.product_variants[0].flavour[0].mrp_price}
              off={data.product_variants[0].flavour[0].discount}
              type={data.product_variants[0].veg_nonveg}
              slug={data.slug}
              key={index}
              variantSlug={data.product_variants[0].slug}
              weightSlug={data.product_variants[0].weight.slug}
              flavorSlug={data.product_variants[0].flavour[0].slug}
            />
          ))}
        </div>
      </Container>
    </Arrow>
  );
};

export default ProductWidget;
