import { useState } from "react";
import { BrowserRouter as Router, Link } from "react-router-dom";
import styles from "./MainNav.module.css";
import logo from "./../../../../assets/img/orealfit-logo.png";
import { ReactComponent as SearchIcon } from "./../../../../assets/icons/search.svg";
import { ReactComponent as UserIcon } from "./../../../../assets/icons/person-outline.svg";
import { ReactComponent as CartIcon } from "./../../../../assets/icons/cart-outline.svg";
import LoginModal from "../../../UI/Modal/LoginModal";

function MainNav(props) {
  const [clickLogin, setClickLogin] = useState(false);
  function clickLoginHandler(){
    setClickLogin(!clickLogin);
  }

  return (
    <div className={styles.header__center}>
      <div className={styles.header_container}>
        <div className={styles.header__logo}>
          <Link to="/">
            <img
              className={styles.header__logo_img}
              src={logo}
              alt="realfit logo"
            />
          </Link>
        </div>
        <form action="#" className={styles.search}>
          <input
            type="text"
            placeholder="Search Product or Brand"
            className={styles.search__input}
          ></input>
          <button className={styles.search__button}>
            <SearchIcon
              className={`${styles.search__icon} ${styles.header__icon}`}
            />
          </button>
        </form>
        {clickLogin && <LoginModal onClick={clickLoginHandler}></LoginModal>}
        <div className={styles.header__right}>
          {/* <Link to="/login" > */}
          <div className={styles.login} onClick={clickLoginHandler}>
            <UserIcon
              className={`${styles.user_icon} ${styles.header__icon}`}
            />
            {/* <div className={styles.user_text}>Login</div> */}
          </div>
          {/* </Link> */}
          <Link to="/checkout/cart">
            <div className={styles.cart}>
              <CartIcon
                className={`${styles.cart_icon} ${styles.header__icon}`}
              />

              <span className={styles.cart__quantity}>5</span>
              {/* <label className={styles.cart_text}>Cart</label> */}
            </div>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default MainNav;
